    .row {
        margin-bottom: 20px;
    }
    #todo {
        width: 100%;
    }
    #input-image {
        min-height: 300px;
    }
    #input-image img {
        width: 100%;
        height: auto;
    }
    .display-image {
        width: 100%;
        margin-bottom: 10px;
    }
    .display-image img {
        width: 100%;
        height: auto;
    }
    #select-image {
        display: none;
    }
    #select-image-label {
        display: inline-block;
        width: 100px;
        text-align: center;
        background-color: #f90;
        color: #fff;
        border-radius: 10px;
        padding: 8px;
    }
    #post-button {
        width: 100%;
        border: none;
        background-color: #4169E1;
        color: #fff;
        padding: 8px;
        border-radius: 10px;
        font-size: 24px;
    }

    #result-modal {
        padding: 20px;
    }

    #close-result-button {
        margin-top: 20px;
        border: none;
        width: 100px;
        text-align: center;
        background-color: #f90;
        color: #fff;
        border-radius: 10px;
        padding: 8px;
        font-size: 16px;
    }
</style>



