<template>
    <v-ons-page id="landing">
        <div class="bg">
            <div class="bg-over">
            <div class="center">
                <h1>LOGIN</h1>
                <div  class="input">
                    <label>メールアドレス</label><br>
                    <input type="text" placeholder="example@yahoo.co.jp">
                </div>
                <div  class="input">
                    <label>パスワード</label><br>
                    <input type="password">
                </div>
                <div style="margin-bottom: 100px">
                    <button class="login-btn" @click="login">ログイン</button>
                </div>
                <span class="link" @click="window.open('http://google.com', '_system')">アカウント作成</span>
            </div>
            </div>
        </div>
    </v-ons-page>
</template>

<script>
import PageMixin from './PageMixin'
import Page from './index'
export default {
    mixins: [ PageMixin ],
    methods: {
        login() {
            this.pushPage(Page.TodoList)
        }
    }
}
</script>

<style scoped>
.bg {
    width: 100%;
    height: 100%;
    background-image: url(https://s3-ap-northeast-1.amazonaws.com/real-juice/resource/back-image.jpg);
    background-size: cover;
}
.bg-over {
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.6);
}
.bg :before {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    border-right: 30px solid rgba(0, 0, 0, 0);
    border-top: 30px solid #f90;
    content: "";
}
.bg :after {
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    border-left: 30px solid rgba(0, 0, 0, 0);
    border-bottom: 30px solid #f90;
    content: "";
}
.center {
    text-align: center;
    padding: 20px;
}

.input {
    margin: 0 auto;
    width: 260px;
    margin-bottom: 80px;
    text-align: left;
}
.input label {
    width: 100%;
    text-align: left;
}
.input input {
    border: none;
    width: 100%;
    border-bottom: solid 1px;
    font-size: 24px;
    text-align: center;
}

.login-btn {
    border: none;
    width: 260px;
    font-size: 18px;
    padding: 5px;
    color: #ffffff;
    background-color: #dc5236;
}

.link {
    color: #25a6d9;
    border-bottom: solid 1px #25a6d9;
}
</style>



